.reviews {
    background: #f2f2f2;
    h2 {
        margin-bottom: 100px;
    }
    .col-lg-3 {
        padding: 0 10px;
    }
    .card {
        padding-top: 80px;
        min-height: 420px;
        border-radius: 10px;
        &-img-top {
            position: absolute;
            width: auto;
            top:-75px;
            left: 50%;
            margin-left: -75px;

        }
        &-title {
            margin-bottom: 0;
        }
        &-age {
            color: #97425f;
            margin-bottom: 20px;
        }
        &-text {
            line-height: 18px;
        }
        margin-bottom: 100px;
    }
    .margin-top {
        margin-top: -70px;
    }
    &__btn {
        text-transform: uppercase;
        outline: none;
        background: #97425f;
        width: 215px;
        line-height: 50px;
        border-radius: 25px;
        border:none;
        color: #fff;
        
    }

    
    
    .panel {
        display: none;
    }
    .panel2 {
        display: none;
    }
    
}