.header {
    color: #fff;
    background: #101010 url(../img/main-bg.jpg) no-repeat center top/cover;
    padding-top: 6.770833333333333vh;
    height: 100vh;
    
        
        nav {
            ul {
                display: flex;
                justify-content: space-between;
                padding-left: 0;
                width: 100%;
                li {
                    list-style-type: none;
                    a {
                        text-decoration: none;
                        text-transform: uppercase;
                        color: #fff;
                        transition: all 0.3s ease;
                        font-size: 14px;
                        font-weight: 700;
                        &:hover {
                            color: #7c384f;
                        }
                    }
                    
                }
                .active {
                    a {
                        color: #7c384f;
                    }
                }

            }
        }


        //MENU


    .main-title {
        margin-top: 9.0625vh;
        &__h1 {

        }
        &__text {
            font-size: 18px;
            margin-top: 7.291666666666667vh;
        }
        &__btn {
            margin-top: 17.39583333333333vh;
            width: 100%;
            button {
                color: #fff;
                background: #97425f;
                border: none;
                text-decoration: none;
                text-transform: uppercase;
                line-height: 70px;
                background: #97425f;
                width: 390px;
                border-radius: 40px;
                outline: none;
                font-size: 16px;
            }
        }
                /* Modal Content */
        /* The Modal (background) */
        .modal {
            display: none; /* Hidden by default */
            position: fixed; /* Stay in place */
            z-index: 99; /* Sit on top */
            padding-top: 100px; /* Location of the box */
            left: 0;
            top: 0;
            width: 100%; /* Full width */
            height: 100%; /* Full height */
            overflow: auto; /* Enable scroll if needed */
            background-color: rgb(0,0,0); /* Fallback color */
            background-color: rgba(0,0,0,0.6); /* Black w/ opacity */
        }
        /* Modal Content */
        .modal-content {
            background-color: #f4f4f4;
            margin: auto;
            padding: 20px;
            border: 1px solid #888;
            width: 80%;
            color: #323234;
            min-width: 400px;
            line-height: 1.2;
            padding-bottom: 50px;
            padding-top: 0;
            z-index: 999;
            form {
                button {
                    color: #fff;
                    background: #97425f;
                    border: none;
                    text-decoration: none;
                    text-transform: uppercase;
                    line-height: 70px;
                    background: #97425f;
                    width: 65%;
                    border-radius: 40px;
                    outline: none;
                    font-size: 16px;
                    margin-top: 50px;
                }
                input {
                    width: 80%;
                    border: none;
                    border-radius: 10px;
                    height: 60px;
                    margin-top: 20px;
                    padding-left: 30px;
                    
                    &:focus {
                        outline: #97425f;
                    }
                }
            }
        }

        /* The Close Button */
        .close {
            color: #97425f;
            
            font-size: 42px;
            font-weight: bold;
        }

        .close:hover,
        .close:focus {
            color: #97425f;
            text-decoration: none;
            cursor: pointer;
        }
    }
}

