.header-contacts {
    background: #2b2b2d url(../img/contacts/bg-contacts.jpg) no-repeat center top/cover;
    height: 100px;
    padding: 0;
    
    .brand {
        height: 100px;
        margin-left: -15px;
        a {
            text-transform: uppercase;
        font-size: 20px;
        font-weight: 700;
       
        text-decoration: none;
        color: #fff;
        }
        
    }
    nav {
        height: 100px;
        ul {
            .active {
                a {
                    color: #7c384f;
                    
                }
            }

        }
    }
    .tel {
        height: 100px;
        margin-right: -15px;
        justify-content: flex-end;
        a {
            text-transform: none;
            text-decoration: none;
            color: #fff;
            font-size: 18px;
            font-weight: 700;
            line-height: 1.2;
            &:hover {
                color: #7c384f;
            }
            
        }
    }
}
.contacts {
    overflow: hidden;
    line-height: 1.2;
    padding-bottom: 0;
    padding-top: 0;
    .padding-top-row {
        padding-top: 5.52083333333351vh;
    }
    .container-fluid {
        max-width: 1200px;
    }
    .row {
        background: #fff;
    }
    .col-padding-top {
        padding-left: 0;
    }
    &-link {
        font-size: 14px;
        padding-left: 3vw;

        a {
            color: #7c384f;
            text-decoration: underline;
            text-transform: none;
            font-size: 14px;
        }
    }
    &-contant {
        padding-left: 3.0vw;
        max-width: 350px;

        &-h1 {
            font-size: 60px;
            margin-left: 0;
            padding-left: 0;
            font-weight: normal;
        }
        &__str {
            margin-top: 2.0vh;
            a {
                color: #97425f;
                text-decoration: none;
                text-transform: none;
                text-decoration: underline;
            }
        }
        &__tel {
            margin-top: 2.8vh;
        
            a {
                font-weight: 700;
                font-size: 20px;
                line-height: 30px;
                text-decoration: none;
                text-transform: none;
                color: #000;
                
            }
        }
        &__icons {
            margin-top: 3.358333333333346vh;
            a {
                margin-right: 1.1vw;
               
                img {
                }
            }
            
        }
        &__text {
            margin-top: 9.9vh;
            width: 110%;
            h6 {
                font-size: 18px;
                font-weight: 700;
                padding: 0;
                margin: 0;
            }
        }
    }
    &-form {
        background-color: #f4f4f4;
        padding: 20px 43px 40px 43px;
        width: 94.5%;
        color: #323234;
        min-width: 300px;
        max-height: 540px;
        border-radius: 10px;
        h3 {
            font-size: 30px;
            font-weight: 700;
        }
        form {
            text-align: center;
            margin-top: 10px;
            
            input {
                width: 100%;
                height: 100%;
                border: none;
                border-radius: 10px;
                height: 5.8vh;
                margin-top: 20px;
                padding-left: 30px;
                &:focus {
                    outline: #97425f;
                }
                
                &::placeholder {
                    color: #323234;
                }
            }
            textarea {
                width: 100%;
                border: none;
                border-radius: 10px;
                height: 11.5vh;
                margin-top: 20px;
                padding-left: 30px;
                padding-top: 2.604166666675vh;
                resize: none; /* Запрещаем изменять размер */
                &:focus {
                    outline: #97425f;
                }    
                &::placeholder {
                    color: #323234;
                }  
            }
            button {
                color: #fff;
                background: #97425f;
                border: none;
                text-decoration: none;
                text-transform: uppercase;
                line-height: 3.5vw;
                background: #97425f;
                width: 87%;
                border-radius: 40px;
                outline: none;
                font-size: 16px;
                margin-top: 2.85416666666679vh;
                letter-spacing: 2px;
            }

        }
    }
      
    
    .contacts-img-1 {
        position: absolute;
        top: 5px;
        left: -129px;
        z-index: -1;
    }
    .contacts-img-2 {
        position: absolute;
        bottom:-0.9vh     ;
        right: -82px;
        z-index: -1;
    }
    .contacts-img-3 {
        position: absolute;
        top: 281px;
        left: -182px;
        z-index: -2;
    }
    .contacts-img-4 {
        position: absolute;
        top:-4vh;
        right: -178px;
        z-index: -1;
    }
}

.submited {
    border: 2 #01BB03 solid;
    width: 75%;
    margin: 0 auto;
    padding: 10px;
    margin-top: 20px;
    margin-bottom: -10px;
    display:none;
    line-height: 35px;
}