
.services {
    .col-lg-4 {
        padding: 0 8px;
    }
    .title {
        margin-bottom: 50px;
        &__p {
            color: #8d8e90;
        }
    }
    .card {
        background: #f7f7f7;
        height: 560px;
        border-radius: 10px;
        width: 100%;
        position: relative;
        border: none;
        margin-bottom: 20px;
        overflow: hidden;
        &-img-top {
            padding: 10px 10px 10px 10px; 
            z-index: 1;
        }
        &-body {
            padding: 20px 0px 32px 30px;
            ul {
                padding-left: 16px;
                li {
                    font-size: 16px;
                }
            }
            .short {
                line-height: 21px;
            }
            &__btn {
                text-transform: uppercase;
                position: absolute;
                width: 215px;
                color: white;
                background: #97425f;
                padding: 14px;
                border-radius: 25px;
                font-weight: 700;
                bottom: 28px;
                z-index: 2;
                border: none;
                outline: none;
            }
        }
        &__sun {
            position: absolute;
            z-index: 0;
            &-1 {
                bottom: -150.5px;
                right: -193.5px;
            }
            &-2 {
                top: 18px;
                right: -204.5px;
            }
            &-3 {
                bottom: -174.5px;
                right: -114.5px;
            }
            &-4 {
                bottom: 62.5px;
                right: 238px;
            }
            &-5 {
                bottom: -178px;
                right: 159px;
            }
            &-6 {
                bottom: 206px;
                right: -222px;
            }
        }  
    }
    .cart-top {
        position: absolute;
        display: block;
        top: 0;
        left: -100px;
    }
    .sun-left {
        position: absolute;
        display: block;
        top: 373px;
        left: -163px;
    }
    .sun-right {
        position: absolute;
        display: block;
        top: 0px;
        right: -163px;
        &-2 {
            z-index: -1;
        }
    }
    .cart-bottom {
        position: absolute;
        display: block;
        bottom: -22px;
        right: -72px;
    }
}

  