.larisa {
    background: #f8f8f8;
    padding-top: 80px;
    position: relative;
    .larisa-hidden {
        height: 50%;
    }
    .larisa-img {
        position: absolute;
        height: 76.56249999999995vh;
        width: 50%;
        top: 0px;
        left: 0vw;
        margin-top: -45px;
        margin-left: -200px;
        z-index: 0;
    }
    .larisa-text {
        .larisa-float {
            float: left;
            width: 45%;
            padding-right: 20px;
        }
        h3 {
        }
        .larisa-text__p {

            &-italic {
                font-style: italic;
            }
            &-italic-color {
                color: #8d8e90;
            }
        }
    }
}