.panacea, .consultation, .services-panacea, .map-panacea{
    padding: 50px 0 0 0;
}
.map-panacea {
    padding-bottom: 50px;
}
.panacea {
    .container {
        background: #fff;
        position: relative;
    }
    span {
        color: #97425f;
    }
    &-link {
        padding-left: 0;
    }
    h2 {
        padding-left: 15px;
    }
    &__img{
        color: #fff;
        background: url(../img/panacea/bg-panacea.jpg) no-repeat center center/cover;
        width: 100%;
        padding: 7.01250000025vh 30px 5.729166666685vh 30px;

        h3 {
            font-size: 48px;
            text-transform: uppercase;
            margin-bottom: 10px;
        }
    }

    &-img-1 {
        position: absolute;
        z-index: -1;
        top: -238px;
        left: -125px;
    }
    .sun-11 {
        top: 124px;
        left: -173px
    }
    .sun-22 {
        left: -173px
    }
    .sun-33 {
        right: -186px;

    }
    &-img-2 {
        position: absolute;
        z-index: -1;
        bottom: 625px;
        right: -79px;
    }
    .sun-44 {
        z-index: -1;
        bottom: 489px;
        right: -186px;
    }
}

.consultation {
    .submited2 {
        margin: 20px 20px -10px 0;
        text-align: center;
    }
    .container {
        background: #f7f7f7;
        padding: 50px 0;
        position: relative;
        overflow: hidden;
    }
    &-form {
        padding: 0;
        padding-top: 0;
        margin-top: -10px;
        padding-left: 20px;
        margin-right: 0;
        background: #f7f7f7;
        color: #000;
        h3 {
            font-size: 48px;
            padding-top: 0;
            margin-top: 0;
            white-space: nowrap;
            font-weight: normal;
            margin-bottom: 0;
        }
        form {
            margin-top: 15px;
            input {
                height: 60px;
                padding-left: 35px;
                width: 90%;
            }
            button {
                line-height: 70px;
                width: 78%;
                margin-top: 3.416666666679vh;
            }
        }
    }
    .consultation-img1 {
        position: absolute;
        top: -32px;
        left: -221px;
    }
    .consultation-img2 {
        position: absolute;
        bottom: -142px;
        right: -192px;
        z-index: 1;
    }
}

.services-panacea {
    position: relative;
    .sun-left2 {
        bottom: -80px;
        left: -180px;
        position: absolute;
        z-index: -1;
    }
    .container {
        background: #fff;
    }
    .panel2 {
        display: none;
    }
    &__btn {
        background: #fff;
        color: #97425F;
        border: 2px solid #97425F;
        line-height: 45px;
        width: 320px;
        font-weight: 700;
    }
}

.panacea2 {
    h3 {
        margin-top: 10px;
        margin-bottom: 35px;
    }
    &__img {
        background: url(../img/panacea/bg-panacea2.jpg) no-repeat center center/cover;
    }
}

.free {
    background: #8c3754;
    color: #fff;
    padding: 42px 0;
    margin-top: 55px;
    overflow: hidden;

    &-form {
        
        h3 {
            font-size: 48px;
            letter-spacing: 2px;

        }
        &-text {
            font-size: 16px;
            letter-spacing: 0.9px;
        }
        &__inputs {
            margin-top: 40px;
            input {
                width: 500px;
                border-radius: 10px;
                padding-left: 30px;
                font-size: 18px;
                line-height: 62px;
                outline: none;
                border: none;
            }
            .name {
                margin-right: 15px;
            }
        }
        &__btn {
            margin-top: 47px;
            width: 433px;
            border: 2px solid #fff;
            outline: none;
            background: #8c3754;
            color: #fff;
            border-radius: 40px;
            height: 77px;
            font-size: 18px;
            font-weight: 700;
            text-transform: uppercase;
            letter-spacing: 1.7px;

        }
    }
    &-img1 {
        position: absolute;
        top: 20px;
        left: -270px;
        z-index: 0;
        transform: rotate(-35deg);
    }
    &-img2 {
        position: absolute;
        top: 44px;
        right: -260px;
        z-index: 0;
    }
}







