.about {
    line-height: 1.2;
    overflow: hidden;
    .container {
        
    }
    .col {
        padding: 0 3.75vw;
    }
    .col-10 {
        padding: 0 3.75vw;
    }
    &__img {
        margin-top: 40px;
        width: 100%;
    }
    .content {
        &__lable {
            font-size: 24px;
            font-weight: 700;
            margin-top: 40px;
            margin-bottom: 35px;
        }
        span {
            color: #97425f;
            text-decoration: underline;
        }
        &__grey {
            color: #8d8e90;
        };
        &__bg-grey {
            background: #f7f7f7;
            padding: 2.2vw 4.7vw 2.2vw 4.25vw;
            font-style: italic;
        }
        strong {
            font-size: 20px;
            font-weight: 700;
        }
        ol {
            list-style-type: none; /* Убираем исходные маркеры */
            counter-reset:  item; /* Обнуляем счетчик списка */
            padding-left: 20px;
            li {
                &:before {
                    content: counter(item) ') '; /* Добавляем к числам скобку */
                    counter-increment: item; /* Задаём имя счетчика */
                    font-weight: 700;
                    line-height: 26px;
                   }
               }
            }

    }
    .sun-1 {
        position: absolute;
        z-index: 0;
        top: 66px;
        left: -200px;
    }
    .sun-2 {
        position: absolute;
        z-index: 0;
        bottom: 43px;
        left: -200px;
        
    }
    .sun-3 {
        position: absolute;
        z-index: 0;
        top: -443px;
        right: -200px;
    }
    .sun-4 {
        position: absolute;
        z-index: 0;
        bottom: 535px;
        right: -200px;
    }
    .row {
        position: relative;
    }
    .col {
        background: #fff;
        opacity: 1;

    }
}
  