body {
    font-family: 'Philosopher', sans-serif;
    color: #323234;
    font-weight: 400;
    font-size: 16px;
    overflow-x: hidden;
    h1 {
        font-weight: 700;
        font-size: 80px;
    }
    h2 {
        font-size: 60px;
    }
    h3 {
        font-size: 36px;
        font-weight: 700;
    }
    h4 {
        font-size: 20px;
        font-weight: 700;
    }
    section {
        padding: 56px 0;
    }
    button {
        outline: none;
        &:focus {
            outline: none;
        }
    }
    @media screen and (max-width: 767px) {
        h2 {
            font-size: 50px;
        } 
    }
    @media screen and (max-width: 500px) {
        h2 {
            font-size: 46px;
        } 
        h3 {
            font-size: 34px;
        }
    }
    @media screen and (max-width: 420px) {
        h2 {
            font-size: 38px;
        } 

    }
   
    
    // 0,1041666666666667 vh
    // 0,05 vw
}

