    @media screen and (max-width: 575px) {
        .navBurger {
            position: fixed;
            right: 30px;
            top: 30px;
            cursor: pointer;
            background: #fff;
            box-sizing: border-box;
            background-clip: content-box;
            width: 30px;
            height: 24px;
            border-top: 11px solid transparent;
            border-bottom: 11px solid transparent;
            -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
            -webkit-transition: all 0.25s ease-in-out;
            transition: all 0.25s ease-in-out;
            z-index: 99;
            &:before,
            &:after {
                content: "";
                position: absolute;
                background: #fff;
                height: 2px;
                width: 100%;
                right: 0;
                will-change: rotate;
                -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
                -webkit-transition: all 0.5s ease;
                transition: all 0.5s ease;
            }
            &:before {
                top: -10px;
            }
            &:after {
                bottom: -10px;
                width: 100%;
            }
            &.active {
                background: transparent;
                background-clip: content-box;
            }
            &.active:before {
                -webkit-transform: rotate(135deg);
                transform: rotate(135deg);
                top: 0;
                background: #eee;
                width: 110%;
            }
            &.active:after {
                bottom: 0;
                -webkit-transform: rotate(-135deg);
                transform: rotate(-135deg);
                background: #eee;
                width: 110%;
            }
        }
        
        .overlay {
            position: fixed;
            background: #2a3140;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            transform-origin: right top;
            transform: scale(0);
            visibility: hidden;
            transition: all 0.4s ease-in-out;
            overflow: hidden;
            z-index: 8;
            
            &.open {
                opacity: 0.97;
                visibility: visible;
                transform: scale(1);
                li {
                    display: block;
                    animation: fadeInRight 0.4s cubic-bezier(0.42, 0.83, 0.7, 1.5)
                        forwards;
                    animation-delay: 0.4s;
                    transform: rotate(-7deg);
                    transform-origin: right bottom;
                    &:nth-of-type(2) {
                        animation-delay: 0.5s;
                    }
                    &:nth-of-type(3) {
                        animation-delay: 0.6s;
                    }
                    &:nth-of-type(4) {
                        animation-delay: 0.7s;
                    }
                    &:nth-of-type(5) {
                        animation-delay: 0.8s;
                    }
                }
            }
        }
        
        .overlayMenu {
            position: absolute;
            text-align: right;
            right: 10vw;
            top: 10vh;
            z-index: 9;
            
            ul {
                list-style: none;
                padding: 0;
                margin: 0 auto;
                display: block;
                position: relative;
                height: 100%;
                li {
                    display: block;
                    position: relative;
                    opacity: 0;
                    width: 100%;
                    a {
                        display: inline-block;
                        border: 1px solid rgba(255, 255, 255, 0.09);
                        color: #fff;
                        text-decoration: none;
                        font-size: 30px;
                        padding: 10px 40px 10px 40px;
                        margin: 5px 0;
                        transition: 0.2s;
                        &:hover,
                        &:active,
                        &:focus {
                            transform: translateX(-2px);
                            padding-left: 80px;
                            outline: 1px solid rgba(255, 255, 255, 0.3);
                            background: rgba(255, 255, 255, 0.03);
                        }
                        
                    }
                }
                .active {
                    a {
                        color: #7c384f;
                    }
                }
            }
        }
        
        .locked {
            overflow: hidden;
        }
        
        @keyframes fadeInRight {
            0% {
                opacity: 0;
                left: 50px;
            }
            100% {
                opacity: 1;
                left: 0;
                transform: rotate(0deg);
            }
        }
        
        // ---------------- Display --------------------
    }
